import {mapState,mapGetters,mapActions,mapMutations} from "vuex"

var options = {
	token: true
};
export default {
	components:{
	},
	data() {
		return {
			
		}
	},
	props:{
		item:{
			type:Object,
			default:null
		}
	},
	computed:{
		...mapState({
			orderList:state=>state.order.orderList
		}),
		...mapGetters([
		]),
	},
	methods: {
		cancelOrder(orderID) {
			uni.showModal({
				title: '提示',
				content: '确认是否取消？',
				success: res => {
					if (res.confirm) {
						this.cancelOrderSure(orderID)
					}
				}
			});
		},
		cancelOrderSure(orderID){
			var _self = this;
			var postData = {
				orderID: orderID
			}
			_self.$H.post('/api/Order/CancelOrder', postData, options).then(res => {
				console.log(res)
				if(res.status == 0){
					var temp = _self.orderList
					for (let i = 0; i < temp.length; i++) {
						if(temp[i].ID == orderID){
							temp[i].Status = 4
							temp[i].StatusName = '用户取消'
							break
						}
					}
				}else{
					uni.showToast({title:res.message, icon:'none', duration:1500})
				}
			});
		},
		openOrderDetail(orderID) {
			uni.navigateTo({
				url:'../../pages/order/detail?orderID=' + orderID,
				animationType: 'pop-in',
				animationDuration: 200
			});
		},
		gotoPay(orderID) {
			uni.navigateTo({
				url:'../../pages/payment/payment-order?orderID='+orderID
			})
		},
	}
}
